                   </span>
                </div>
                <div style="display: grid;grid-gap: 10px;grid-template-columns: auto auto;place-self: center end;text-align: right;">
                  <a  v-bind:href="'#/tenant/groups/new' + '/' + tenantId + '/' + tenantName" class=" uk-button uk-button-small uk-button-default ui_button_small_green ui_button_small"
                    style="cursor: pointer;background: rgb(144, 194, 62);color: white !important;line-height: 25px;min-width: 125px;border: 0.05em solid rgb(131, 181, 50);line-height: 32px !important;border-radius: 3px;">
