<style scoped>
  .uk-dropdown-nav {
      font-size: .975rem;
  }
</style>


<template>

  <div style="background: whitesmoke;;display:grid;grid-template-columns:1fr;width:100%;place-items:start center;overflow-y:overlay" class="custom-scroll-bar">


      <div class="stage" style="display: grid;place-items: start center;">
          <div class="" style="padding-top:50px;width: 50%;">

              <form class="uk-grid-small" uk-grid style="font-family:lato" onsubmit="return false;">

                  <div class="uk-width-1-1">
                      <label class="uk-form-label" for="form-stacked-text">Tenant <span uk-icon="icon:triangle-right;ratio:0.65"></span> Groups <span uk-icon="icon:triangle-right;ratio:0.65"></span> Add New Group</label>
                      <div class="uk-form-controls">
                          <div class="uk-inline uk-width-1-1">
                              <span class="uk-form-icon" uk-icon="icon: album;ratio:0.75"></span>
                              <input id="firstName" type="text" class="uk-input" v-model="tenantName" style="font-family: Lato;font-weight: 300;background-color: #f7f7f7;" autofocus="true" readonly>
                          </div>
                      </div>
                  </div>

                  <div class="uk-width-1-1">
                    <hr class="uk-divider-icon">
                  </div>

                  <div class="uk-width-1-1">
                      <label class="uk-form-label" for="form-stacked-text">Name</label>
                      <div class="uk-form-controls">
                          <div class="uk-inline uk-width-1-1">
                              <span class="uk-form-icon" uk-icon="icon: users;ratio:0.75"></span>
                              <input id="firstName" type="text" class="uk-input"  placeholder="Name"  v-model="groupObject.name" style="font-family: Lato;font-weight: 300;" autofocus="true">
                          </div>
                      </div>
                  </div>

                  <div  class="uk-width-1-1">
                      <label class="uk-form-label" for="form-stacked-text">Status</label>
                      <div class="uk-form-controls">
                          <div class="uk-inline uk-width-1-1">
                              <select  class="uk-select" name="status" v-model="groupObject.status" style="font-family: Lato;font-weight: 300;height: 35px;">
                                  <option value="ACTIVE">ACTIVE</option>
                              </select>
                          </div>
                      </div>
                  </div>


                  <div  class="uk-width-1-1">
                        <label class="uk-form-label" for="form-stacked-text">Provide a short description</label>
                        <textarea class="uk-textarea" rows="5" placeholder="" id="description"  v-model="groupObject.description"></textarea>
                  </div>

                  <div class="uk-margin">
                      <div class="uk-inline">
                          <button id="saveButton" class="clickable-btn uk-button uk-button-danger uk-button-small uk-grid-margin uk-first-column end-call-button" style="background-color: rgb(240, 80, 110); border-radius: 2px; place-self: center start; place-items: center; min-width: 100px; font-size: 0.65rem; line-height: 32px; font-weight: normal !important;" v-on:click="save()" >Save</button>
                          &nbsp;&nbsp;&nbsp;
                          <button class="clickable-btn uk-button uk-button-danger uk-button-small uk-first-column end-call-button" style="background-color: rgba(253, 253, 253, 0.99); color: rgb(111, 111, 111); border: 1px solid rgb(183, 183, 183); border-radius: 3px; place-self: center start; place-items: center; min-width: 100px; font-size: 0.65rem; line-height: 32px; font-weight: normal !important;" v-on:click="goBack()" >Exit</button>
                      </div>
                  </div>
                </form>

            </div>
        </div>
    </div>
</template>


<script>



import userMixinLib                   from './../../mixins/lib/user_mixin_lib';
import utilsMixinLib                  from './../../mixins/lib/utils_lib';

    export default{
        mixins :    [
                        userMixinLib,
                        utilsMixinLib
                    ],
        props: [],
        data: function () {
            return {
                appName: "/oct.io",
                tenantId : this.$route.params.tenant_id,
                tenantName : this.$route.params.tenant_name,
                groupObject :{
                    id : this.$route.params.id,
                }
            };
        },
        methods: {

            goBack: function(){
              this.$router.push({
                  name: 'tenant-groups-list',
                  params: {
                    tenant_id : this.tenantId,
                    tenant_name: this.tenantName
                  }
              });
            },
            doValidation : function()
            {

                if(this.groupObject.name===undefined || this.groupObject.name.trim()==="")
                {
                      UIkit.notification("<span uk-icon='icon: warning;ratio: 0.65'></span> Please enter a valid Name.", {
                                          status: 'danger',
                                          pos: 'top-center',
                                          timeout: 5000
                                      });

                      return false;
                }
                else if(this.groupObject.status===undefined || this.groupObject.status.trim()==="")
                {
                      UIkit.notification("<span uk-icon='icon: warning;ratio: 0.65'></span> Please enter a valid Status.", {
                                          status: 'danger',
                                          pos: 'top-center',
                                          timeout: 5000
                                      });

                      return false;
                }
                else if(this.groupObject.description.trim()==="")
                {
                      UIkit.notification("<span uk-icon='icon: warning;ratio: 0.65'></span> Please enter a description.", {
                                          status: 'danger',
                                          pos: 'top-center',
                                          timeout: 5000
                                      });

                      return false;
                }

                return true;
            },
            getRecord : function(){

                console.log("getRecord");
                let _this_component = this;

                if(_this_component.groupObject.id === "new")
                {
                    _this_component.groupObject = {
                        id:"new"
                    };
                    return false;
                }

                let get_url = "./groups/get/" + _this_component.groupObject.id;
                console.log(get_url);

                axios.get(get_url)
                        .then(function (dataResponse) {

                            console.log(dataResponse);
                            if(dataResponse.data["actionResult"]==1)
                            {
                                let groupObject = dataResponse.data["bean"];
                                _this_component.groupObject = groupObject;
                                return false;
                            }

                        });


            },
            save : function(){

                if(!this.doValidation())
                    return false; // stop here if form is invalid.


                let btnText = document.getElementById("saveButton").innerHTML;
                document.getElementById("saveButton").innerHTML = "Saving..";
                this.disableHTMLElement(document.getElementById("saveButton"));

                let _this_component = this;
                let post_url = "./../groups/save";
                console.log("Posting data to : " + post_url);




                let form = {
                    id :     "new",
                    name :   this.groupObject.name,
                    status  :   this.groupObject.status,
                    description     :   this.groupObject.description,
                    tenantId     :   this.tenantId
                }

                let formString = JSON.stringify(form,null,null);


                // VueJS ajax call-1
                axios.post(post_url, form)
                          .then((dataResponse) => {

                            console.log("Group Registration Result : ");
                            console.log(dataResponse);

                            if(dataResponse.data["actionResult"]===1)
                            {
                                _this_component.newGroupInfo = dataResponse.data["bean"];

                                UIkit.notification("<span uk-icon='icon: check;ratio: 0.65'></span> New Group added successfully",{
                                    status: 'success',
                                    pos: 'top-right',
                                    timeout: 5000
                                });

                                document.getElementById("saveButton").innerHTML = btnText;
                                this.enableHTMLElement(document.getElementById("saveButton"));
                            }
                            else
                            {
                                alert("Error : " + dataResponse)
                            }


                    })
                    .catch(function(errorResponse) {

                        console.log("ERROR MS - ", errorResponse);
                        let exceptionMsg = errorResponse.body["exception"];

                        UIkit.notification("<span uk-icon='icon: warning ;ratio: 0.65'></span> " + exceptionMsg + ".", {
                                            status: 'danger',
                                            pos: 'top-right',
                                            timeout: 500000
                                        });


                        document.getElementById("saveButton").innerHTML = btnText;
                        this.enableHTMLElement(document.getElementById("saveButton"));
                        return false;
                    });
            },
            logInfo: function (msg){

                let consoleLog = this.consoleLogTxt + " \n " + msg;
                this.consoleLogTxt = consoleLog;

                if (this.displayConsoleLog)
                {
                    console.log(msg);
                }
            },
        },
        mounted: function () {
            this.getRecord();
        },
        created: function () {
            console.error("WELCOME TO NEW USER REGISTRATION FORM.");
        }
    }

</script>
